import React from 'react'
import { Link as RLink } from "react-router-dom";
import {
    Events,
    scrollSpy,
    scroller
} from "react-scroll"
import { useEffect, useState } from 'react';

import CustomList from '../common/CustomList';

const TopBar = () => {
    const [selectedLanguage, setLanguage] = useState(require('../../resources/flags/uk_flag.jpg').default);

    const items = [
        {
            route: '/',
            name: 'Home',
            jsx:
                <RLink to='/'>
                    { 'Home' }
                </RLink>
        },
        {
            route: '/menu',
            name: 'Menu',
            jsx:
                <RLink to='/menu'>
                    { 'Menu' }
                </RLink>
        },
        {
            route: '/about',
            name: 'About',
            jsx:
                <RLink to='/about'>
                    { 'About' }
                </RLink>
        },
        {
            route: '/contact',
            name: 'Contact',
            jsx:
                <RLink to='/contact' onClick={() => scrollTo()}>
                    { 'Contact' }
                </RLink>
        },
    ];

    const scrollTo = (offset = -170, avoidHiding = false) => {
        /*if(this.props.isTransitionMenu === true && avoidHiding === false) {
            this.props.hideMenu()
        }else {
            if(this.props.isTransitionMenu === true)
                return
        }*/

        scroller.scrollTo('main', {
            duration: 300,
            smooth: true,
            spy: true,
            offset: offset
        })
    };

    const uk_lang = require('../../resources/flags/uk_flag.jpg').default;
    const es_lang = require('../../resources/flags/es_flag.jpg').default;

    return (
        <div id='topbar' className='prevent-text-selection'>
            <div className='page-container'>
                <div>
                    <CustomList items={items} listOrientation={'h'} />
                </div>
            </div>

            <div id='language-selector'>
                <img src={selectedLanguage} />

                <div className='dropdown'>
                    <img src={uk_lang} onClick={() => setLanguage(uk_lang)} />
                    <img src={es_lang} onClick={() => setLanguage(es_lang)} />
                </div>
            </div>
        </div>
    )
}

export default TopBar
