import React from 'react'
import { 
    GoogleMap,
    withScriptjs, 
    withGoogleMap,
    Marker,
} from 'react-google-maps'

const loc = {
    lat: 36.3592,
    long: -5.2308
};

function InitialMap() {

    return (
        <GoogleMap
            defaultZoom={15}
            defaultCenter={{lat: loc.lat, lng: loc.long}}>
            {/* <Marker position={{lat: lat, lng: long}}
                icon={{
                    url: logo,
                    scaledSize: new window.google.maps.Size(100, 30)
                }}
            /> */}
            <Marker position={{lat: loc.lat, lng: loc.long}} />
        </GoogleMap>
    )
}

const WrappedMap = withScriptjs(withGoogleMap(InitialMap));

const Map = () => {
    return (
        <div id="google-map-container">
            {/* <WrappedMap 
                googleMapURL={`https://maps.googleapis.com/maps/api/js?v=3.exp&libraries=geometry,drawing,places&key=${process.env.REACT_APP_GOOGLE_KEY}`} 
                loadingElement={<div style={{ height: '100%' }} />}
                containerElement={<div style={{ height: '100%' }} />}
                mapElement={<div style={{ height: '100%' }} />} 
            /> */}
            <div className='mapouter'>
                <div className='gmap_canvas'>
                    <iframe
                        title='APRICUS LOCATION'
                        src={`https://maps.google.com/maps?q=${loc.lat},%20${loc.long}&t=&z=13&ie=UTF8&iwloc=&output=embed`}
                        frameborder="0" scrolling="no" marginheight="0" marginwidth="0"></iframe>
                </div>
            </div>
        </div>
    )
}

export default Map