import React from 'react'

import {
    EmailShareButton,
    FacebookShareButton,
    FacebookMessengerShareButton,
    HatenaShareButton,
    InstapaperShareButton,
    LineShareButton,
    LinkedinShareButton,
    LivejournalShareButton,
    MailruShareButton,
    OKShareButton,
    PinterestShareButton,
    PocketShareButton,
    RedditShareButton,
    TelegramShareButton,
    TumblrShareButton,
    TwitterShareButton,
    ViberShareButton,
    VKShareButton,
    WhatsappShareButton,
    WorkplaceShareButton
} from "react-share";

import {
    EmailIcon,
    FacebookIcon,
    FacebookMessengerIcon,
    HatenaIcon,
    InstapaperIcon,
    LineIcon,
    LinkedinIcon,
    LivejournalIcon,
    MailruIcon,
    OKIcon,
    PinterestIcon,
    PocketIcon,
    RedditIcon,
    TelegramIcon,
    TumblrIcon,
    TwitterIcon,
    ViberIcon,
    VKIcon,
    WeiboIcon,
    WhatsappIcon,
    WorkplaceIcon
} from "react-share";

import { MdOutlineContentCopy } from 'react-icons/md'

const ShareWebsite = () => {
    const websiteURL = 'https://bojiix.github.io/ApricusGHPDeploy/';

    return (
        <div className='share-website-container'>
            <div>
                <div className='social-medias'>
                    <FacebookShareButton url={websiteURL}>
                        <FacebookIcon size={40} round={true} />
                    </FacebookShareButton>

                    <FacebookMessengerShareButton url={websiteURL}>
                        <FacebookMessengerIcon size={40} round={true} />
                    </FacebookMessengerShareButton>

                    <WhatsappShareButton url={websiteURL}>
                        <WhatsappIcon size={40} round={true} />
                    </WhatsappShareButton>

                    <LinkedinShareButton url={websiteURL}>
                        <LinkedinIcon size={40} round={true} />
                    </LinkedinShareButton>

                    <TelegramShareButton url={websiteURL}>
                        <TelegramIcon size={40} round={true} />
                    </TelegramShareButton>

                    <LineShareButton url={websiteURL}>
                        <LineIcon size={40} round={true} />
                    </LineShareButton>

                    <EmailShareButton url={websiteURL}>
                        <EmailIcon size={40} round={true} />
                    </EmailShareButton>
                </div>

                <div className='copy-to-clipboard prevent-text-selection' onClick={() => {
                    navigator.clipboard.writeText(websiteURL)
                }}>
                    <MdOutlineContentCopy />

                    <p className='text-container one-line marginless-el'>
                        { websiteURL }
                    </p>
                </div>
            </div>
        </div>
    )
}

export default ShareWebsite
