const validateForm = (e, v_type, v_setter) => {
    let value = e.target.value, reg, cond = false;
    
    switch(v_type) {
        case 'name':
            reg = new RegExp("[^a-zA-Z\\- '`]+");
            cond = true;
        break;

        case 'phone':
            reg = new RegExp('^[0-9]+$');
            cond = false;
        break;
    }

    if(value !== '' && reg.test(String(value).toLowerCase()) === cond)
        return;
    
    v_setter(value);
}

export default validateForm;