import React, { useEffect, useState } from 'react'
import { Container, Row, Col, Form, Button } from "react-bootstrap"
import FileAttach from './FileAttach';

import validateForm from './validations/FormValidation';
import sendEmailForm from '../../requests/EmailRequests';
import Booking from './Booking';

const ContactForm = () => {
    const [name, setName] = useState('');
    const [email, setEmail] = useState('');
    const [phone, setPhone] = useState('');
    const [category, setCategory] = useState('');
    const [file, setFile] = useState(null);
    const [subject, setSubject] = useState('')
    const [message, setMessage] = useState('');

    const handleUploadClick = (event) => {
        const files = Array.from(event.target.files)
        setFile(files[0])
    }

    const onSubmit = async (e) => {
        e.preventDefault();

        let formData = new FormData(e.target);
        formData.append('name', name);
        formData.append('email', email);
        formData.append('phone', phone);
        formData.append('category', category)
        formData.append('file', file);
        formData.append('subject', subject);
        formData.append('message', message);

        await sendEmailForm(formData).then(res => {
            console.log('DONE');
        });
    };

    return (
        <div className='contact-form'>
            <Form onSubmit={(e) => onSubmit(e)}>
                <Row>
                    <Col>
                        <Form.Group className="mb-3" controlId="formBasicEmail">
                            <Form.Label>Name</Form.Label>
                            <Form.Control 
                                type="text" 
                                placeholder="Enter name"
                                value={name}
                                onChange={(e) => validateForm(e, 'name', setName)} />
                            <Form.Text className="text-muted">
                                We will never share your personal data with anyone else.
                            </Form.Text>
                        </Form.Group>
                    </Col>
                </Row>

                <Row>
                    <Col>
                        <Form.Group className="mb-3" controlId="">
                            <Form.Label>Email address</Form.Label>
                            <Form.Control 
                                type="email"
                                placeholder="Enter email" 
                                value={email}
                                onChange={(e) => setEmail(e.target.value)} />
                        </Form.Group>
                    </Col>

                    <Col>
                        <Form.Group className="mb-3" controlId="">
                            <Form.Label>Phone Number</Form.Label>
                            <Form.Control 
                                type="text" 
                                placeholder="Enter phone number" 
                                onChange={(e) => validateForm(e, 'phone', setPhone)} />
                        </Form.Group>
                    </Col>
                </Row>

                <Row>
                    <Col>
                        <Form.Group className="mb-3" controlId="">
                            <Form.Label>Category</Form.Label>
                            <Form.Control
                                as="select" 
                                onChange={(e) => setCategory(e.target.value)}>
                                <option value={""}>Choose category</option>
                                <option value={"booking"}>Booking</option>
                                <option value={"hiring"}>Hiring</option>
                            </Form.Control>
                        </Form.Group>
                    </Col>
                </Row>

                <Row>
                    <Col>
                    {
                        category !== '' ?
                            category === 'hiring' ?
                                <FileAttach handleUploadClick={handleUploadClick} file={file} />
                            : category === 'booking' ? <Booking /> : ''
                        : ''
                    }
                    </Col>
                </Row>

                <Row>
                    <Col>
                        <Form.Group className="mb-3" controlId="">
                            <Form.Label>Subject</Form.Label>
                            <Form.Control 
                                type="text" 
                                placeholder="Enter subject"
                                onChange={(e) => setSubject(e.target.value)} />
                        </Form.Group>
                    </Col>
                </Row>

                <Row>
                    <Col>
                        <Form.Group className="mb-3" controlId="">
                            <Form.Label>Message</Form.Label>
                            <Form.Control 
                                as="textarea" 
                                rows={5} placeholder="Enter message"
                                onChange={(e) => setMessage(e.target.value)} />
                        </Form.Group>
                    </Col>
                </Row>

                <Row>
                    <Col>
                        <Button variant="primary" type="submit">
                            Submit
                        </Button>
                    </Col>
                </Row>
            </Form>
        </div>
    )
}

export default ContactForm
