import React, { Component, useEffect } from 'react'

import { MdOutlineClose } from 'react-icons/md'

class CutsomModal extends Component {

    constructor(props) {
        super(props);

        this.wrapperRef = React.createRef();
        this.handleClickOutside = this.handleClickOutside.bind(this);
    }

    componentDidMount() {
        document.addEventListener('mousedown', this.handleClickOutside);
    }

    componentWillUnmount() {
        document.removeEventListener('mousedown', this.handleClickOutside);
    }

    handleClickOutside = (e) => {
        if (this.props.display === true && this.wrapperRef && !this.wrapperRef.current.contains(e.target)) {
            this.props.toggleShareModal(false)
        }
    }

    render() {
        return (
            <div ref={this.wrapperRef} className={`modal-container ${this.props.display ? 'display-block' : 'display-none'}`}>
                <div className='close-modal'>
                    <div>Share</div>
                    <MdOutlineClose onClick={() => this.props.toggleShareModal(false)} />
                </div>
                {
                    this.props.content
                }
            </div>
        )
    }
}

export default CutsomModal
