import React from 'react'
import ContactForm from '../forms/ContactForm'
import { Row, Col } from 'react-bootstrap'

import Map from './Map'
import ContactDetails from './ContactDetails'

const Contact = () => {
    return (
        <div id='contact-container'>
            
            <div className='page-container'>
                <div>
                    <Row>
                        <Col>
                            <h1>Contact</h1>
                            <p>
                                Do not hesitate to contact us for any information regarding reservations, menus, offers, employment.
                            </p>
                        </Col>
                    </Row>

                    <Row>
                        <Col>
                            <ContactDetails />
                        </Col>

                        <Col>
                            <ContactForm />
                        </Col>
                    </Row>
                </div>
            </div>

            <Map />
        </div>
    )
}

export default Contact
